<template>
  <div class="emoji">
    <!-- 输入框区域 -->
    <div :class="faceShow ? 'box boxFaceShow' : 'box'" ref="heightFace">
      <!-- 输入框 -->
      <!-- 传过来的值是rename,用textContent接收,我觉得不能用placeholder接受吧,好像用这个接收也行 -->
      <textarea v-model="textArea" :placeholder="textConent"></textarea>

      <!-- faceContent显示出整个表情面板 -->
      <p class="faceBut" @click="faceContent">
        <!-- 图标 -->
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-biaoqing"></use>
        </svg>
        表情
      </p>
    </div>
    <!-- 表情区域 -->
    <div class="browBox" v-if="faceShow">
      <ul>
        <li
          v-for="(item, index) in faceList"
          :key="index"
          @click="getBrow(index)"
        >
          {{ item }}
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
// 导入JSON格式的表情库
const appData = require("@/assets/emojis.json");
export default {
  name: "Emoji",
  data() {
    return {
      // 输入的留言内容
      textArea: '',
      // 表情列表
      faceList: [],
      // 是否显示表情面板区域
      faceShow: false,
      // 从众多表情中选中的
      getBrowString: "",
      // 保存提交的留言   
      content: [],
    };
  },
  props: {
    // MegBox传过来的  rename
    textConent: {
      type: String,
      default: ''
    }
  },
  methods: {
    // 点击页面中出现的那个表情，显示出整个表情面板（含很多表情）
    faceContent() {
      this.faceShow = !this.faceShow;
      if (this.faceShow == true) {
        for (let i in appData) {
          this.faceList.push(appData[i].char);
        }
      } else {
        this.faceList = [];
      }
    },
    // 获取用户点击之后的表情 ，存放到输入框内
    getBrow(index) {
      for (let i in this.faceList) {
        if (index == i) {
          this.getBrowString = this.faceList[index];
          this.textArea += this.getBrowString;
        }
      }
    },
    // 输入的留言存入到content中    点击MegBox.vue中的提交留言按钮
    referContent() {
      if (this.textArea == "") return alert("请输入内容");
      // 存入
      this.content.push(this.textArea);
      // 清空input数据
      this.textArea = "";
      // 关闭表情列表
      this.faceShow = false;
    },
  }
};
</script>
